.edit-user{
    width: 85%;
    direction: rtl;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.edit-user form{
    display: flex;
    flex-direction: column;
    margin: 5em auto;
    width: 25%;
}

.add-user .form-div{
    display: flex;
    flex-direction: column;
    width: 25%;
    justify-content: space-between;
    margin: 0 auto;
}

.edit-user .form-div div{
    display: flex;
    align-items: center;
    margin: 0.5em 0;
}

.edit-user .form-div div :first-child {
    text-align: start;
}

.edit-user .form-div div * {
    width: 50%;
}