.confirm-requests{
    width: 85%;
    direction: rtl;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.confirm-requests select{
    height: 100%;
    width: 100%;
    font: inherit;
    text-align: center;
    border: none;
}

.confirm-requests .waiting{
    background-color: rgb(207, 207, 207);
}
.confirm-requests .confirmed{
    background-color: rgb(141, 255, 131);
}
 .not-confirmed{
    background-color: rgb(255, 127, 127);
}

.confirm-requests option{
    background-color: white;
}