.old-requests{
    width: 85%;
    direction: rtl;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.old-requests .waiting{
    background-color: rgb(207, 207, 207);
}
.old-requests .confirmed{
    background-color: rgb(141, 255, 131);
}
.old-requests .not-confirmed{
    background-color: rgb(255, 127, 127);
}