*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body{
  background-color: white;
  font-family: 'Inter', sans-serif;
  font-size: 20px;
}

input{
  height: 3em;
  width: 25em;
  color: #676767;
  background-color: #F0F0F0;
  border: none;
  margin: 1em auto;
  text-align: center;
}

input:focus, select:focus {
  outline: none;
}

button{
  background-color: #19388A;
  color: white;
  width: 10em;
  margin: 2em auto;
  padding: 0.5em 2em;
  font-size: 1em;
  cursor: pointer;
}

a{
  display: block;
  margin: auto;
}

h1{
  color: #19388A;
  margin: 2em 0 0 0;
}

table, th, td{
  border: 1px solid #dddddd;
}


table{
  width: 90%;
  margin: 3em auto;
  border-collapse: collapse;
}

th,td{
  height: 3em;
  width: 5em;
}

